.service-process__dl:after {
  content: "";
  display: inline-block;
  position: absolute;
  width: 102px;
  height: 45px;
  background: url(../img/service/ico-arr-down.png) no-repeat center/100%;
  bottom: 35px;
  left: calc(50% - 51px);
}
@media screen and (max-width: 1024px) {
  .service-process__dl:after {
    width: 76px;
  }
}
@media screen and (max-width: 834px) {
  .service-process__dl:after {
    width: 56px;
    height: 20px;
    bottom: 60px;
    left: calc(50% - 28px);
  }
}
.service-process__dl:nth-last-child(2) {
  padding-bottom: 80px;
}
.service-process__dl:nth-last-child(2):after {
  display: none;
}
.service-process__dt {
